<template>
  <div class="app">
    <div class="sidenav">
      <p><router-link :to="{name: 'market_summary'}"> </router-link></p>
      <p><router-link :to="{name: 'report_tnved'}">  </router-link></p>
      <p><router-link :to="{name: 'report_country'}">  </router-link></p>
      <p><router-link :to="{name: 'report_region'}">  </router-link></p>
    </div>
    <div class="main">
      <div class="data-pickers">
        <month-picker-input @input="date_from" :no-default="true" lang="ru"></month-picker-input>
        <month-picker-input @input='date_to' :no-default="true" lang="ru"></month-picker-input>
      </div>
      <input type="text" v-model="item">
<!--      <autocomplete :min-len="1" :placeholder="'  '" :items="items" v-model="item" :get-label="getLabel" :component-item="template" @update-items="updateItems"></autocomplete>-->
      <button @click="addTnved">+</button>
      <div class="tnved_list" v-for="(tnved, index) in selectedTnved" :key="index">
        <span class="div-as-button" @click="rmTnved(index)">{{tnved}}</span>
      </div>
      <br>
      <select v-model="params">
        <option value="stoim"></option>
        <option value="netto"></option>
      </select>
      <select v-model="category">
        <option value=""></option>
        <option value=""></option>
      </select>
      <select v-model="interval">
        <option value="year"></option>
        <option value="quartal"></option>
        <option value="month"></option>
      </select>
      <button @click="getData">  </button>
    </div>
    <div id="page-container">
      <router-view
                :date="date"
                :params="params"
                :interval="interval"
                :category="category"
                :tnved_list="selectedTnved"
        ></router-view>
    </div>
  </div>
</template>


<script>
import { MonthPickerInput } from 'vue-month-picker'
// import Autocomplete from 'v-autocomplete'
import 'v-autocomplete/dist/v-autocomplete.css'
import AutocompleteItemTemplate from "./AutocompleteItemTemplate";
// import {HTTP} from './http-common'

export default {
  name: 'App',
  data() {
      return {
          template: AutocompleteItemTemplate,
          items: [],
          item: null,
          selectedTnved: [],
          date: {
              from: null,
              to: null
          },
          params: 'stoim',
          interval: 'year',
          category: '',
      }
  },
  mounted: function() {
      window.console.log(this.$router)
  },
  methods: {
      addTnved () {
          this.selectedTnved.push(this.item)
          this.item = null
      },
      rmTnved (index) {
          this.selectedTnved.splice(index, 1)
      },
      // updateItems (text) {
      //   HTTP.get('statistic/autocomplete/', {
      //       params: {
      //           'q': text
      //       }
      //   })
      //       .then(response => {
      //           this.items = response.data
      //       })
      //
      // },
      getLabel(item) {
          return item
      },
      getData() {
          this.$eventHub.$emit('recount')
      },
      date_from(data) {
          if (String(data.monthIndex).length === 1) {
              this.date.from = data.year + '-' + '0' + data.monthIndex
          } else {
              this.date.from = data.year + '-' + data.monthIndex
          }
      },
      date_to(data) {
          if (String(data.monthIndex).length === 1) {
              this.date.to = data.year + '-' + '0' + data.monthIndex
          } else {
              this.date.to = data.year + '-' + data.monthIndex
          }
      }
  },
  components: {
    MonthPickerInput,
    // Autocomplete
  },
};
</script>


